.price {
  padding-top: 50px;
  background-color: black;
  padding-bottom: 50px;
  background-image: url("../img/bg-dots-light.svg");
  background-repeat: repeat;

  &__wrapper {
    padding-left: 40px;
    padding-right: 40px;
    margin-left: auto;
    margin-right: auto;
  }

  &__title {
    margin-bottom: 51px;
  }

  &__list {
    margin-left: -40px;
    margin-right: -40px;
  }

  &__item {
    background-color: white;
    padding-top: 28px;
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 36px;
    margin-bottom: 50px;
    margin-left: 10px;
    margin-right: 10px;
    width: 100%;
  }

  &__name {
    font-weight: 800;
    font-size: 16px;
    line-height: 140%;
    text-transform: uppercase;
    color: #000000;
    margin-top: 0;
    margin-bottom: 11px;
  }

  &__cost {
    font-size: 30px;
    line-height: 160%;
    color: #000000;
    margin-bottom: 27px;
  }

  &__include-item {
    padding-left: 20px;
    position: relative;
    margin-bottom: 23px;
    font-weight: normal;
    font-size: 12px;
    line-height: 130%;

    &:last-child {
      margin-bottom: 33px;
    }

    &:before {
      position: absolute;
      left: 0;
      top: 5px;
      display: block;
      content: '';
      width: 6px;
      height: 8px;
      background-image: url("data:image/svg+xml,%3Csvg width='6' height='8' viewBox='0 0 6 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.93407 4L-3.52834e-05 0H1.83144L6 4L1.83144 8H-3.52834e-05L3.93407 4Z' fill='%2300A9CF'/%3E%3C/svg%3E%0A");
    }
  }

  &__btn {
    background-color: black;
    color: white;
    max-width: 366px;
    width: 100%;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 800;
    font-size: 12px;
    line-height: 140%;
    text-align: center;
    text-transform: uppercase;
  }

  .slick-dots{
    bottom: -10px;
    height: 6px;
  }
  .slick-dotted.slick-slider {
    margin-bottom: 55px;
  }
  .slick-dots li{
    height: 6px;
    width: 6px;
  }
  .slick-dots li button{
    width: 10px;
    height: 6px;
    padding: 0;
  }

  .slick-dots li button::before {
    color: white;
    opacity: 1;
    font-size: 16px;
    width: 6px;
    height: 6px;
    line-height: 1;
  }
  .slick-dots li.slick-active button::before{
    color: $main_color;
    font-size: 24px;
    line-height: 0.7;
    margin-left: -1px;
    margin-right: -1px;
  }
}
