.program {
  padding-top: 72px;
  padding-bottom: 80px;

  &__wrapper {
    padding-left: 40px;
    padding-right: 40px;
    max-width: 320px;
    margin-left: auto;
    margin-right: auto;
  }

  &__title {
    color: black;
  }

  &__list {
    margin-top: 35px;
  }

  &__item {
    padding-top: 30px;
    border-top: 1px solid rgba(0, 0, 0, 0.3);
    margin-bottom: 22px;
    &:last-child{
      border-bottom: 1px solid rgba(0, 0, 0, 0.3);
      margin-bottom: 0;
      padding-bottom: 22px;
    }
    &.open{
      .program__accordion{
        transition: .2s;
        max-height: 100%;
        opacity: 1;
        visibility: visible;
      }
      .program__accordion-toggle{
        &:after{
          opacity: 0;
        }
      }
    }
  }

  &__accordion {
    max-height: 0;
    opacity: 0;
    visibility: hidden;
  }

  &__item-head {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    margin-bottom: 8px;
  }

  &__accordion-title {
    font-weight: 800;
    font-size: 12px;
    line-height: 140%;
    text-transform: uppercase;
    color: #000000;
    margin-top: 0;
    margin-bottom: 0;
  }

  &__accordion-toggle {
    position: relative;
    padding: 0;
    margin: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: .2s;

    &:before {
      position: absolute;
      display: block;
      content: '';
      width: 20px;
      height: 1px;
      background-color: black;
      opacity: .3;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: .2s;
    }

    &:after {
      position: absolute;
      display: block;
      content: '';
      width: 1px;
      height: 20px;
      background-color: black;
      opacity: .3;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: .2s;
    }
  }

  &__accordion-text {
    font-size: 12px;
    line-height: 160%;
    color: #000000;
    margin-top: 0;
    margin-bottom: 0;
  }
}
